import React from "react";
import "./PolicyPages.css";

const PrivacyPolicy = () => {
  return (
    <section className="policy-section" style={{ scrollMarginTop: "90px" }}>
      <div className="policy-container">
        <h1 className="policy-title">Privacy Policy</h1>
        <p>
          CrackTest (“we”, “our”, “us”) is committed to protecting your privacy. This policy explains
          how we collect, use, and safeguard your personal information when you use our website and mobile app.
        </p>

        <h2>1. Information We Collect</h2>
        <p>We may collect personal details such as your name, email, phone number, and payment details when you register or subscribe to our services.</p>

        <h2>2. How We Use Your Information</h2>
        <p>Your information is used to provide our services, process payments, improve user experience, and send updates or offers related to CrackTest.</p>

        <h2>3. Payment Security</h2>
        <p>All online payments are processed through secure, PCI-compliant payment gateways such as Easypaisa. We do not store your card or wallet details.</p>

        <h2>4. Data Protection</h2>
        <p>We implement strict security measures to protect your personal information from unauthorized access, alteration, or disclosure.</p>

        <h2>5. Your Rights</h2>
        <p>You have the right to request access to, correction, or deletion of your personal data by contacting us at cracktest.official@gmail.com.</p>

        <h2>6. Changes to this Policy</h2>
        <p>We may update this Privacy Policy from time to time. The updated policy will be posted on this page.</p>

        <p className="last-updated">Last Updated: September 2025</p>
      </div>
    </section>
  );
};

export default PrivacyPolicy;
